
.talion-nav {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 3;
  background-color: white;
}
.talion-nav .header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: 1.5rem 1.8rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-bottom: 1px solid #f3f3f3;
}
.talion-nav .header span {
      color: #42bd56;
      font-size: 1.6rem;
      margin-right: 1.5rem;
}
.talion-nav .header #search {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 3rem;
      border: none;
      background: url(data:image/png;base64,) no-repeat 50% #f3f3f3;
      background-color: #F3F3F3;
      background-size: 1.7rem;
      border-radius: .3rem;
}
.talion-nav .header #search:focus {
      background-image: none;
}
.talion-nav main {
    min-width: 32rem;
}
.talion-nav .nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.talion-nav .nav li {
      -webkit-box-flex: 1;
          -ms-flex: 1 0 auto;
              flex: 1 0 auto;
      width: 33.3%;
      text-align: center;
      font-size: 2.4rem;
      line-height: 1;
      margin-top: 2.5rem;
      color: #2384e8;
}
.talion-nav .nav li div:nth-child(2) {
        font-size: 1.2rem;
        line-height: 1.8;
}
.talion-nav .link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-top: 3rem;
}
.talion-nav .link li {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
      width: 50%;
      color: #42bd56;
      border-top: 1px solid #f3f3f3;
      padding: 1.5rem 0;
}
.talion-nav .link li a {
        display: block;
        text-align: center;
        height: 2rem;
        line-height: 2rem;
        font-size: 1.5rem;
}
.talion-nav .link li:nth-child(3), .talion-nav .link li:nth-child(4) {
      border-bottom: 1px solid #f3f3f3;
}
.talion-nav .link li:nth-child(odd) a {
      border-right: 1px solid #f3f3f3;
}



