
.talion-nav-primary {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  box-sizing: border-box;
  height: 4.6rem;
  border-bottom: 0.1rem solid #f3f3f3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 1.7rem;
  background-color: white;
  min-width: 32rem;
}
.talion-nav-primary nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.talion-nav-primary a {
    text-decoration: none;
    font-size: 1.7rem;
    margin-right: 1.7rem;
    line-height: 2.3rem;
}
.talion-nav-primary .logo {
    width: 4.6rem;
    height: 2.2rem;
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
}
.talion-nav-primary .show-talion-nav {
    background: url(data:image/png;base64,) left center no-repeat;
    width: 2.5rem;
    height: 2.3rem;
    background-size: contain;
}



