
.download {
  text-align: center;
  margin: 8rem 0;
}
.download div {
    margin-right: 4rem;
    margin-bottom: 2rem;
    display: inline-block;
    white-space: pre;
}
.download div span:nth-child(2) {
      font-size: 2.4rem;
      line-height: 2.8rem;
}
.download div span:nth-child(4) {
      font-size: 1.4rem;
      line-height: 2rem;
}
.download div img {
      width: 4.8rem;
      margin-right: 1rem;
      height: 4.8rem;
      float: left;
}
.download a {
    font-size: 1.5rem;
    color: #42bd56;
}



