
.cross-scroller {
  margin-top: 2rem;
}
.cross-scroller .bar {
    padding: 0 5px;
    font-size: 1.6rem;
    font-weight: normal;
}
.cross-scroller .wrapper {
    height: 18.7rem;
    margin-top: 2rem;
    overflow: hidden;
}
.cross-scroller .container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    height: 20.7rem;
}
.cross-scroller .container .item {
      height: 18.7rem;
      width: 10rem;
      -webkit-box-flex: 0;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
      margin: 0 5px;
}
.cross-scroller .container .item img {
        width: 10rem;
        height: 14.3rem;
}
.cross-scroller .container .item .title {
        font-size: 1.4rem;
        overflow: hidden;
        white-space: pre;
        text-align: center;
        text-overflow: ellipsis;
        width: 100%;
}



