
.status {
  margin: 4.6rem 0 0 0;
  padding: 0;
  max-width: none;
  position: relative;
  z-index: 1;
}
.status section {
    color: #2CA532;
    font-size: 1.4rem;
    line-height: 2.4rem;
    position: relative;
}
.status section .bg {
      width: 100%;
      padding-bottom: 21%;
}
.status section .bg img {
        width: 100%;
        position: absolute;
}
.status section .content {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.status section .content span {
        display: inline-block;
        margin-left: 1.8rem;
}
.status section .content div button {
        outline: none;
        border-radius: 3px;
        line-height: 2;
        color: #2CA532;
        background-color: transparent;
        border: 1px solid #2CA532;
        margin-right: 1rem;
        padding: 0 1.4rem;
}
.status section .content div button:nth-child(1) {
        color: white;
        background-color: #2CA532;
}
.status main {
    margin: 1.8rem;
}



