
.subject {
  margin: 4.6rem 0 0 0;
  padding: 0;
  max-width: none;
  position: relative;
  z-index: 1;
}
.subject section {
    color: #2CA532;
    font-size: 1.4rem;
    line-height: 2.4rem;
    position: relative;
}
.subject section .bg {
      width: 100%;
      padding-bottom: 21%;
}
.subject section .bg img {
        width: 100%;
        position: absolute;
}
.subject section .content {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.subject section .content span {
        display: inline-block;
        margin-left: 1.8rem;
}
.subject section .content div button {
        outline: none;
        border-radius: 3px;
        line-height: 2;
        color: #2CA532;
        background-color: transparent;
        border: 1px solid #2CA532;
        margin-right: 1rem;
        padding: 0 1.4rem;
}
.subject section .content div button:nth-child(1) {
        color: white;
        background-color: #2CA532;
}
.subject main {
    margin: 1.8rem;
}
.subject main .title {
      font-size: 2.4rem;
      line-height: 3.2rem;
      margin-top: 2rem;
}
.subject main .rating {
      display: inline-block;
}
.subject main .l-img {
      float: right;
      width: 10rem;
      height: 15rem;
      margin-left: 2.4rem;
}
.subject main .info {
      font-size: 1.4rem;
      line-height: 1.6;
      margin-top: 1.5rem;
}
.subject main .subject-mark {
      color: #42bd56;
      font-size: 1.4rem;
      line-height: 1.5;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 2rem 0;
}
.subject main .subject-mark .item {
        display: block;
        line-height: 3rem;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        border: 1px solid;
        border-radius: 3px;
        text-align: center;
        margin: 0 1rem;
}
.subject main .open-app {
      color: #42bd56;
      font-size: 1.4rem;
      line-height: 1.5;
      margin: 1rem 0;
      display: block;
}
.subject main .sub-title {
      font-size: 1.4rem;
      color: #aaa;
}
.subject main .comments li {
      margin-bottom: 3rem;
}
.subject main .comments img {
      width: 3.6rem;
      height: 3.6rem;
      float: left;
      border-radius: 1.8rem;
      margin-right: 1rem;
}
.subject main .comments .time {
      font-size: 1.2rem;
      color: #aaa;
}
.subject main .comments .comment {
      margin: 1rem 0 1rem 3.6rem;
      font-size: 1.5rem;
      line-height: 2.2rem;
}
.subject main .comments .like {
      margin-left: 3.6rem;
      font-size: 1.4rem;
      color: #aaa;
}
.subject main .comments .like a {
        display: inline-block;
}
.subject main .comments .like .bt-like {
        float: left;
        padding-left: 2rem;
        background: url(../../assets/ic_like_gray.svg) no-repeat;
}
.subject main .comments .like .more {
        background: url(../../assets/more.svg) center center no-repeat;
        float: right;
        width: 3rem;
        height: 2rem;
}



